  left: calc(50% - 60px);
}

.learn .stroke {
  stroke: white;
  stroke-dasharray: 360;
  stroke-dashoffset: 360;
  transition: 0.6s ease-out;
}

.learn svg {
  fill: #17141d;
  stroke-width: 2px;
}

.learn .card:hover .stroke {
  stroke-dashoffset: 100;
  transition: 0.6s ease-out;
}

